<template>
  <div class="row">
    <div class="col-md-12 mb-3 mt-3" id="login">
      <p style="color: white">로그인</p>
    </div>
    <div class="col-md-12">
      <label for="name" class="form-label">ID</label>
      <input type="text" class="form-control" id="name" v-model="id" />
    </div>
    <div class="col-md-12">
      <label for="password" class="form-label">Password</label>
      <input
        type="password"
        class="form-control"
        id="password"
        v-model="password"
      />
    </div>
    <div class="col-md-12">
      <button
        class="btn btn-primary"
        style="width: 100%"
        type="button"
        @click="login"
      >
        로그인
      </button>
    </div>
    <div class="col-md-12">
      <div class="row">
        <div class="col-md-4">
          <button
            style="width: 100%"
            type="button"
            class="btn btn-secondary"
            @click="$router.push({ name: 'join' })"
          >
            회원가입
          </button>
        </div>
        <div class="col-md-4">
          <button style="width: 100%" type="button" class="btn btn-secondary">
            ID 찾기
          </button>
        </div>
        <div class="col-md-4">
          <button style="width: 100%" type="button" class="btn btn-secondary">
            Password 찾기
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      id: "",
      password: "",
    };
  },
  props: {},
  methods: {
    login() {
      const user_id = this.id;
      const user_password = this.password;
      axios
        .post("/root/login", {
          user_id,
          user_password,
        })
        .then((response) => {
          sessionStorage.setItem("user_id", response.data.user_id);
          sessionStorage.setItem("user_name", response.data.user_name);
          this.$store.commit("AddSession");
          this.$router.push("/");
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>

<style scoped>
#login {
  background-image: url("../assets/top-bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 15px;
}

#login p {
  text-align: center;
  margin: auto;
  color: aliceblue;
}

button {
  margin-top: 20px;
  margin-right: 20px;
}
</style>
